import React, { PureComponent } from 'react';
import './index.styl';
import StudyPathItem from './studyPathItem';
import { studyPathItems, StudyPathItemInte } from './config';
interface State {
    data: StudyPathItemInte[];
}
class StudyPath extends PureComponent<any, State> {
    state = {
        data: [],
    };
    componentDidMount() {
        this.setState({
            data: studyPathItems,
        });
    }
    render() {
        const { data } = this.state;
        const finalStudyPathItem: Array<JSX.Element> = data.map(
            (i: StudyPathItemInte, index: number) => {
                const { id, name, icon, product_count } = i;
                return (
                    <StudyPathItem
                        key={id}
                        name={name}
                        product_count={product_count}
                        icon={icon}
                    />
                );
            }
        );
        return (
            <div className="studyPath">
                <div className="top">
                    <div className="left">学习路径</div>
                    <div className="right">查看全部</div>
                </div>
                <div className="studyPathItemWrapper">
                    <div className="content">{finalStudyPathItem}</div>
                </div>
            </div>
        );
    }
}

export default StudyPath;
